<template>
    <div>
        <img :src="imgbase644" alt="">
    </div>
</template>

<script>
export default {
    data() {
        return {
            imgbase64: null
        }
    },
    created() {
        this.imgbase64 = "data:image/jpeg;base64," + this.$route.params.imgbase64;
    },
}
</script>