<template>
  <div id="container">
    <div id="picture"><img src="../assets/confirm.png" /></div>
  </div>
</template>

<script>
import request from '../axios/request'
export default {
  data() {
    return {
      pictureUrl: null,
    };
  },
  created() {
    this.pictureUrl = this.$route.params.pictureUrl;
    // console.log("confirm:", this.pictureUrl);
    this.sendReq().then(res => {
        //根据返回的信息判断跳转人脸识别成功或者失败的路由页面
        // const data1 = JSON.parse(res.data);
        const data = res.data;
        console.log("data--", data)
        console.log("msg--", data.msg, data.msg == 'success')
        console.log("imgbase64--", data.imgbase64)
        if(data.msg == 'success'){
            console.log('success');
            this.$router.push({name:'success', params:{imgbase64: data.imgbase64}}).catch(err => {
                console.log(err);
            });
        }else if(data.msg == 'null'){
            console.log('fail');
            this.$router.push({name:'fail'}).catch(err => {
                console.log(err);
            });
        }else{
           console.log('else');
            this.$router.push({name:'fail'}).catch(err => {
                console.log(err);
            });
        }
        
        
    }).catch(err => {

       console.log(err);
        //捕获错误一律跳转失败失败页面
        this.$router.push({name:'fail'}).catch(err => {
            console.log(err);
        });
    })
  },
  methods: {
      sendReq(){
          
          return request.post('http://zxz.vaiwan.com/API/face', {img: this.pictureUrl})
      }
  },
};
</script>

<style scoped>
#container {
  width: 100%;
  height: 100%;
  background: black;
  overflow: hidden;
}

img{
    margin: 100px auto;
    display: block;
}
</style>